'use client';

import React, { useEffect, useState } from 'react';
import Link from 'next/link';
import { supabase } from '@/lib/supabase';
import { User } from '@supabase/supabase-js';
import { Menu, X, Zap, LogOut, User as UserIcon, LayoutDashboard } from 'lucide-react';
import { cn } from '@/lib/utils';

export default function Navbar() {
  const [user, setUser] = useState<User | null>(null);
  const [isOpen, setIsOpen] = useState(false); // Mobile menu state
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    // 1. Get initial user
    const getUser = async () => {
      const { data } = await supabase.auth.getUser();
      setUser(data.user);
    };
    getUser();

    // 2. Listen for auth changes (Login/Logout)
    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setUser(session?.user ?? null);
    });

    // 3. Handle scroll effect
    const handleScroll = () => {
      setScrolled(window.scrollY > 20);
    };
    window.addEventListener('scroll', handleScroll);

    return () => {
      subscription.unsubscribe();
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const handleLogout = async () => {
    await supabase.auth.signOut();
    setIsOpen(false);
    window.location.href = '/'; // Hard redirect to clear any state
  };

  return (
    <nav
      className={cn(
        "fixed w-full top-0 z-50 transition-all duration-300 border-b border-transparent",
        scrolled 
          ? "bg-black/80 backdrop-blur-md border-cyan-900/30 shadow-[0_4px_20px_-10px_rgba(34,211,238,0.2)]" 
          : "bg-transparent"
      )}
    >
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16">
          
          {/* LOGO */}
          <div className="flex-shrink-0 flex items-center">
            <Link href="/" className="group flex items-center space-x-2">
              <div className="relative">
                <div className="absolute -inset-1 bg-cyan-500 rounded-full opacity-20 group-hover:opacity-40 blur transition-opacity"></div>
                <Zap className="h-6 w-6 text-cyan-400 relative z-10" />
              </div>
              <span className="font-bold text-xl tracking-tighter text-white">
                JACKPOT<span className="text-cyan-400 group-hover:text-cyan-300 transition-colors">INTEL</span>
              </span>
            </Link>
          </div>

          {/* DESKTOP MENU */}
          <div className="hidden md:block">
            <div className="ml-10 flex items-baseline space-x-4">
              <Link 
                href="/guide" 
                className="text-gray-300 hover:text-white hover:bg-white/5 px-3 py-2 rounded-md text-sm font-medium transition-all"
              >
                How it Works
              </Link>

              {user ? (
                <>
                  <Link
                    href="/dashboard"
                    className="flex items-center space-x-2 bg-gray-800 hover:bg-gray-700 text-white px-4 py-2 rounded-lg text-sm font-bold transition-all border border-gray-700 hover:border-cyan-500/50"
                  >
                    <LayoutDashboard className="w-4 h-4 text-cyan-400" />
                    <span>Dashboard</span>
                  </Link>
                  
                  {/* NEW: Account Link */}
                  <Link
                    href="/account"
                    className="text-gray-400 hover:text-white px-3 py-2 rounded-md text-sm font-medium transition-colors flex items-center"
                  >
                    <UserIcon className="w-4 h-4 mr-1" />
                    My Barracks
                  </Link>

                  <button
                    onClick={handleLogout}
                    className="text-gray-400 hover:text-red-400 px-3 py-2 rounded-md text-sm font-medium transition-colors flex items-center"
                  >
                    <LogOut className="w-4 h-4 mr-1" />
                    Sign Out
                  </button>
                </>
              ) : (
                <Link
                  href="/login"
                  className="bg-cyan-500 hover:bg-cyan-400 text-black px-5 py-2 rounded-lg text-sm font-bold transition-all shadow-[0_0_15px_rgba(34,211,238,0.4)] hover:shadow-[0_0_25px_rgba(34,211,238,0.6)] flex items-center"
                >
                  <UserIcon className="w-4 h-4 mr-2" />
                  Login
                </Link>
              )}
            </div>
          </div>

          {/* MOBILE MENU BUTTON */}
          <div className="-mr-2 flex md:hidden">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-white hover:bg-gray-800 focus:outline-none"
            >
              {isOpen ? <X className="block h-6 w-6" /> : <Menu className="block h-6 w-6" />}
            </button>
          </div>
        </div>
      </div>

      {/* MOBILE MENU DROPDOWN */}
      <div className={cn("md:hidden bg-gray-900 border-b border-gray-800", isOpen ? "block" : "hidden")}>
        <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3">
          <Link
            href="/guide"
            onClick={() => setIsOpen(false)}
            className="text-gray-300 hover:text-white hover:bg-gray-800 block px-3 py-2 rounded-md text-base font-medium"
          >
            How it Works
          </Link>

          {user ? (
            <>
              <Link
                href="/dashboard"
                onClick={() => setIsOpen(false)}
                className="text-white bg-gray-800 block px-3 py-2 rounded-md text-base font-bold"
              >
                Dashboard
              </Link>
              
              {/* NEW: Mobile Account Link */}
              <Link
                href="/account"
                onClick={() => setIsOpen(false)}
                className="text-gray-300 hover:text-white hover:bg-gray-800 block px-3 py-2 rounded-md text-base font-medium"
              >
                My Barracks
              </Link>

              <button
                onClick={handleLogout}
                className="w-full text-left text-gray-400 hover:text-red-400 block px-3 py-2 rounded-md text-base font-medium"
              >
                Sign Out
              </button>
            </>
          ) : (
            <Link
              href="/login"
              onClick={() => setIsOpen(false)}
              className="text-cyan-400 font-bold hover:text-cyan-300 block px-3 py-2 rounded-md text-base"
            >
              Login / Join
            </Link>
          )}
        </div>
      </div>
    </nav>
  );
}